#root{
    color: #777777;
    font-family: Lato;
    font-size: 14px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  
  *, *:before, *:after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
  }
    
  textarea{
    color: inherit;
    font-family: inherit;
    font-size: inherit;
  }
  
  /**********************************/
  /*Share Comments*/
  /*********************************/
  
  .shareCommentContainer{
    background: none repeat scroll 0 0 #f5f5f5;
     margin: 20px 10px;
    border-radius: 4px;
    width: 60%;
  }
  
  .shareCommentContainer textarea {
    height: 100px;
    padding: 20px;
    width: 100%;
    border: 1px solid #F2F2F2;
    outline: none;
    border-radius: 4px 4px 0 0;
    
  }
  
  .shareCommentContainer .btn-success{
    padding: 6px 15px;
    margin-top: 9px;
    margin-bottom: 9px;
    margin-left: 9px;
    border: medium none;
    border-radius: 4px;
    background-color: #5cb85c;
    font-size: 14px;
    letter-spacing: 0.3px;
    color: #fff;
  }
  
  .shareCommentContainer button:hover{
    cursor: pointer;
    background-color: #008000;
  }
  
  
  /**********************************/
  /*Display Comments*/
  /*********************************/
  
  .commentContainer {
    position: relative;
    max-width:  60%;
    height: auto;
    margin: 20px 10px;
    padding: 10px;
    border: 1px solid #f5f5f5;
    -webkit-box-shadow: rgba(0, 0, 0, 0.3) 7px 7px 7px;
            box-shadow: rgba(0, 0, 0, 0.3) 7px 7px 7px;
    border-radius: 4px;  
  }
  
  .commentText {
    min-height: 30px;
    border-radius: 3px;
    font-family: Arial;
    font-size: 14px;
    line-height: 1.5;
    color: #797979;
  }
  
  .commentText > textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #F2F2F2;
    border-radius: 4px 4px 0 0;
  }
  
  .commentContainer button{
    margin-right : 10px;
    margin-top: 20px;
    padding: 6px 12px;
    color: #333;
    background-color: #F5F5F5;
    border: medium none;
    border-radius: 4px;
    border: 1px solid #DCDCDC;
    outline:none;
  }
  
  .commentContainer button:hover{
    cursor: pointer;
    background-color: #e6e6e6;
    border-color: #adadad;
  }


